import { graphql, Link, PageProps } from 'gatsby';
import { MDXRenderer } from 'gatsby-plugin-mdx';
import React from 'react';
import { Helmet } from 'react-helmet';
import { PrimaryButton } from '../components/common/Buttons/Buttons';
import Layout from '../components/common/Layout/Layout';
import CustomLink from '../components/common/Link';
import { GatsbySeoComponent, TopLevelPageJSONLDSchemas } from '../components/common/SEO';
import {
  StyledBackground,
  TermButtons,
  TermContainer,
  TermDefinitionContainer,
  TermTitle,
  StyledTermPage,
  TermDefinition,
} from '../components/term/styled';
import { InternalLinks } from '../utils/links';
import { Text } from '../components/common/Text';
import { mapTermNodeToGlossaryTerm } from '../mappers';

const TermPage = ({ data }: PageProps<Queries.TermPageQuery>) => {
  const { title } = data.mdx?.frontmatter!;
  const description = data?.mdx?.excerpt;
  const allTerms = data.allMdx.edges.map((edge) => mapTermNodeToGlossaryTerm(edge.node));
  const currentTermIndex = allTerms.findIndex((item) => item.id === data.mdx?.id);
  const nextTerm = allTerms[currentTermIndex + 1] ?? allTerms[0];

  return (
    <>
      <Helmet title={`${title} | Telgorithm`}>
        <meta property="og:title" content={title} />
        <meta property="twitter:title" content={title} />
        <meta name="description" content={description as string}></meta>
        <meta property="og:description" content={description as string} />
        <meta property="twitter:description" content={description as string} />
      </Helmet>
      <TopLevelPageJSONLDSchemas
        url={'https://www.telgorithm.com/glossary/' + data.mdx?.slug}
      />
      <GatsbySeoComponent
        canonicalUrl={'https://www.telgorithm.com/glossary/' + data.mdx?.slug}
      />
      <Layout disabledContainer>
        <StyledTermPage>
          <StyledBackground></StyledBackground>
          <TermContainer>
            <TermTitle>
              <Text.Regular.Normal.md>Term</Text.Regular.Normal.md>
              <Text.Title.Bold.md>{title}</Text.Title.Bold.md>
            </TermTitle>
            <TermDefinitionContainer>
              <Text.Regular.Normal.md>Definition</Text.Regular.Normal.md>
              <TermDefinition>
                <MDXRenderer children={data?.mdx?.body ?? ''} />
              </TermDefinition>
            </TermDefinitionContainer>
            <TermButtons>
              <Link to={InternalLinks.Glossary}>
                <PrimaryButton>Back To Glossary</PrimaryButton>
              </Link>
              <CustomLink arrow address={nextTerm?.url}>
                View Next Term
              </CustomLink>
            </TermButtons>
          </TermContainer>
        </StyledTermPage>
      </Layout>
    </>
  );
};

export default TermPage;

export const query = graphql`
  query TermPage($id: String) {
    mdx(id: { eq: $id }) {
      frontmatter {
        title
      }
      body
      id
      slug
      excerpt(pruneLength: 150)
    }
    allMdx(
      filter: { fileAbsolutePath: { regex: "//terms//" } }
      sort: { fields: frontmatter___title, order: ASC }
    ) {
      edges {
        node {
          frontmatter {
            title
          }
          id
          slug
        }
      }
    }
  }
`;
