import styled from 'styled-components';
import { colors } from '../../utils/colors';
import { device } from '../../utils/layoutBreakpoints';
import { StyledTemplateContent } from '../common/styled';

export const StyledBackground = styled.div`
  background-color: ${colors.secondary.gray100};
  height: 400px;
  width: 100%;
  box-sizing: border-box;
`;

export const TermContainer = styled.div`
  position: relative;
  background-color: ${colors.primary.white};
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 50px;
  top: -300px;
  padding: 40px;
  max-width: 70%;
  @media ${device.laptop} {
    top: -200px;
    padding: 60px;
    max-width: 1000px;
  }
`;

export const TermTitle = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  gap: 10px;
  margin-bottom: 20px;
  span {
    color: ${colors.secondary.gray400};
  }
  @media ${device.laptop} {
    margin-bottom: 0px;
    gap: 30px;
  }
`;

export const TermDefinitionContainer = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
  & > span {
    color: ${colors.secondary.gray400};
  }
  @media ${device.laptop} {
    gap: 20px;
  }
`;
export const TermDefinition = styled.div`
  font-weight: 400;
  font-size: 0.8rem;
  line-height: 1.1rem;
  ${StyledTemplateContent};
`;

export const TermButtons = styled.div`
  display: flex;
  flex-direction: column;
  gap: 15px;
  align-items: center;
  margin-top: -20px;
  @media ${device.tablet} {
    flex-direction: row;
    gap: 30px;
    margin-top: 0px;
  }
`;

export const StyledTermPage = styled.div`
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;
